import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { AdunitService } from '../../adunit.service';
import { BucketService } from '../../bucket.service';
import { bucket } from '../bucket/bucket.interface';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {

private bucketObj: bucket = {
    unit_name: null,
    unit_price: null,
    unit_description: null
};

angForm: FormGroup;

constructor (private formBuilder: FormBuilder,
             private adService: AdunitService,
             private _bucketService: BucketService) {

}

ngOnInit () {
  this.angForm = this.formBuilder.group({
    unit_name: ['', Validators.required],
    unit_price: ['', Validators.required],
    unit_description: ['', Validators.required]
  } )
}

addUnit(name: string, price: number, description: string) {
  this.adService
  .addAdUnit(name, price)
  .subscribe(res => console.log('done'));

  this.bucketObj.unit_name = name;
  this.bucketObj.unit_price = price;
  this.bucketObj.unit_description = description;
  this._bucketService.getData(this.bucketObj)

}

}
