import { Component, OnInit, OnChanges } from '@angular/core';
import { BucketService } from '../../bucket.service';
import { Subscription } from 'rxjs/Subscription';
import { AdunitService } from './../../adunit.service';
import { bucket } from './../../models/bucket.model';

@Component({
  selector: 'app-bucket',
  templateUrl: './bucket.component.html',
  styleUrls: ['./bucket.component.css']
})
export class BucketComponent implements OnInit, OnChanges {
  private subscription: Subscription;
  public val: bucket[];

  constructor(private _bucketService: BucketService, private _adunitservice: AdunitService) { }

  ngOnInit() {
    this.subscription = this._adunitservice.getAdUnits()
    .subscribe(
      (arg : bucket[]) => {
        this.val = arg;
      }
    )
    this.subscription = this._bucketService.nameChange
    .subscribe(
      (arg: bucket) => {
        this.val.push(arg);
    }
    );
  }

  deleteProduct (index: number){
    this.val.splice(index,1);
  }

  ngOnChanges() {
  }

}
